import React, { useState } from 'react'
import { Link } from 'react-router-dom';
import "./Register.css";
import axios from 'axios';
import baseURL from '../../api/BaseUrl';
import Swal from 'sweetalert2';
const Register = () => {
    const [state, setState] = useState({
        fullname: '',
        email: '',
        phone_number: '',
        address: '',
        password: '',
        confirmPassword: ''
    });

    // khi to validation
    const [errors, setErrors] = useState({
        fullname: '',
        email: '',
        phone_number: '',
        address: '',
        password: '',
        confirmPassword: ''
    });

    // hm input change
    const handleChange = (e) => {
        let { name, value } = e.target;
        setState((pre) => ({ ...pre, [name]: value }));
    }

    // check validation
    const validate = () => {
        let newErrors = {};
        let isValid = true;

        // check name
        if (!state.fullname) {
            newErrors.fullname = "Vui lng nhp tn ng nhp.";
            isValid = false;
        } else if (/\d/.test(state.fullname)) {
            newErrors.fullname = "Tn khng c cha s.";
            isValid = false;
        } else if (state.fullname.length < 5) {
            newErrors.fullname = "Tn ng nhp phi t nht 5 k t.";
            isValid = false;
        }

        // check email
        if (!state.email) {
            newErrors.email = "Vui lng nhp email.";
            isValid = false;
        } else if (!/^[^\s@]+@[^\s@]+\.[a-zA-Z]{3}$/.test(state.email)) {
            newErrors.email = "Email khng hp l.";
            isValid = false;
        }

        // check phone
        if (!state.phone_number) {
            newErrors.phone_number = "Vui lng nhp s in thoi.";
            isValid = false;
        } else if (!/^\d+$/.test(state.phone_number)) {
            newErrors.phone_number = "S in thoi ch c cha s.";
            isValid = false;
        } else if (state.phone_number.length !== 10) {
            newErrors.phone_number = "S in thoi ch c ng 10 s.";
            isValid = false;
        }

        // check address
        if (!state.address) {
            newErrors.address = "Vui lng nhp a ch.";
            isValid = false;
        }

        // check password
        if (!state.password) {
            newErrors.password = "Vui lng nhp mt khu.";
            isValid = false;
        }
        if (!state.confirmPassword) {
            newErrors.confirmPassword = "Vui lng xc nhn mt khu.";
            isValid = false;
        } else if (state.confirmPassword !== state.password) {
            newErrors.confirmPassword = "Mt khu xc nhn khng khp.";
            isValid = false;
        }
        setErrors(newErrors);
        return isValid;
    }
    // handle register
    const handleRegister = async (e) => {
        e.preventDefault();
        if (!validate()) {
            return;
        }
        try {
            const data = await axios.post(baseURL + "users/register", state);
            console.log(data)
            await Swal.fire({
                title: 'ng k thnh cng!',
                icon: 'success',
                showConfirmButton: false,
                timer: 500
            })
            window.location.href = "/login";
        } catch (error) {
            alert("ng k tht bi, vui lng th li!");
        }
    }

    return (
        <div className="register-container">
            <h2>ng k</h2>
            <form onSubmit={handleRegister}>
                <div className="form-group">

                    <input onChange={handleChange} type="text" id="fullname" name="fullname" placeholder="Tn ng nhp" />
                    {errors.fullname && <p style={{ color: 'red' }} className="error">{errors.fullname}</p>}

                </div>
                <div className="form-group">
                    <input onChange={handleChange} type="email" id="email" name="email" placeholder="Email" />
                    {errors.email && <p style={{ color: 'red' }} className="error">{errors.email}</p>}
                </div>
                <div className="form-group">
                    <input onChange={handleChange} type="text" id="phone_number" name="phone_number" placeholder="S in thoi" />
                    {errors.phone_number && <p style={{ color: 'red' }} className="error">{errors.phone_number}</p>}

                </div>
                <div className="form-group">
                    <input onChange={handleChange} type="text" id="address" name="address" placeholder="a ch" />
                    {errors.address && <p style={{ color: 'red' }} className="error">{errors.address}</p>}
                </div>
                <div className="form-group">
                    <input onChange={handleChange} type="password" id="password" name="password" placeholder="Mt khu" />
                    {errors.password && <p style={{ color: 'red' }} className="error">{errors.password}</p>}
                </div>
                <div className="form-group">
                    <input onChange={handleChange} type="password" id="confirmPassword" name="confirmPassword" placeholder="Xc nhn mt khu" />
                    {errors.confirmPassword && <p style={{ color: 'red' }} className="error">{errors.confirmPassword}</p>}
                </div>
                <div className="form-group">
                    <button type="submit22">ng k</button>
                </div>
            </form>
            <div className="login-link">
                <p> c ti khon? <Link to="/login">ng nhp</Link></p>
            </div>
        </div>
    );
};


export default Register