import React from 'react';
import './Tuyendung.css';

const Tuyendung = () => {
  return (
    <div className="recruitment-container">
      <h1 className="heading">Tham Gia i Ng Ca Chng Ti</h1>
      <p className="subheading">Chng ti ang tm kim nhng c nhn ti nng  gia nhp i ng nng ng ca chng ti.</p>

      <div className="job-listings">
        <div className="job-card">
          <h2 className="job-title">Nhn Vin Bn Hng</h2>
          <p className="job-description">
            Chng ti cn tuyn Nhn Vin Bn Hng vi kinh nghim trong lnh vc bn l v chm sc khch hng.
            Yu cu kh nng giao tip tt v lm vic theo ca.
          </p>
          <button className="apply-button">ng Tuyn Ngay</button>
        </div>

        <div className="job-card">
          <h2 className="job-title">Nhn Vin Kho</h2>
          <p className="job-description">
            Tm kim Nhn Vin Kho vi kinh nghim qun l hng ha v sp xp kho.
            Yu cu cn thn, t m v c sc khe tt.
          </p>
          <button className="apply-button" style={{ marginTop: '20px' }}>ng Tuyn Ngay</button>
        </div>

        <div className="job-card">
          <h2 className="job-title">Nhn Vin Marketing</h2>
          <p className="job-description">
            Cn tuyn Nhn Vin Marketing sng to v nng ng, c kinh nghim trong qung co trc tuyn v qun l mng x hi.
            Thnh tho cng c qung co v c kh nng lm vic c lp.
          </p>
          <button className="apply-button">ng Tuyn Ngay</button>
        </div>
      </div>
    </div>
  );
}

export default Tuyendung;
